import React, { Component } from 'react'
import './index.css'
import fetchJsonp from 'fetch-jsonp'
import Rating from '../rating'

class Detail extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data: {
        author: [],
        rating: [],
        images: [],
        tags: []
      },
      id: this.props.location.state.id
    }
    console.log('id', this.state.id)
  }
  // 
  componentWillMount () {
    const self = this
    fetchJsonp('https://api.douban.com/v2/book/' + this.state.id)
      .then(response => {
        return response.json()
      }).then(data => {
        console.log('parsed data', data)
        self.setState({
          data: data
        })
      }).catch(ex => {
        console.log('parsing failed', ex)
      })
  }
  render () {
    const data = this.state.data
    return (
      <div>
        <div className='back'>
          <a className='back-btn'></a>
          <span>{data.title}</span>
        </div>
        <div className='intro'>
          <div className='left'>
            <h1>{data.title}</h1>
            <p className='rating-text'>
              <Rating item={data.rating} />{data.rating.average}
              <span className='num-raters'>{data.rating.numRaters}</span>
            </p>
            <p className='meta'>
              {data.author.map((string) => {
                return (
                  <span>{string}</span>
                )
              })}
              {data.publisher} / {data.pubdate} / {data.binding} / {data.pages} / {data.price}</p>
            <p className='tag-name'>
              {data.tags.map((string) => {
                return (
                  <span>{string.name}</span>
                )
              })}
            </p>
            {/* <p>:  / </p>
            <p>:  /  /  /  /  / </p>
            <p>: </p>
            <p>/: </p>
            <p>: </p>
            <p>: 2018-07-27()</p>
            <p>: 118</p> */}
          </div>
          <img src={data.images.small} className='cover' />
        </div>
        <div className='info'>
          <h2></h2>
          <p>{data.summary}</p>
        </div>
        <div className='info'>
          <h2></h2>
          <p>{data.author_intro}</p>
        </div>
      </div>
    )
  }
}

export default Detail
